.dialogWindow {
    position: fixed;
    top: 80vh;
    left: 50vw;
    /* to center the dialogue window */
    transform: translate(-50%, -50%);
    width: 60vw;
    z-index: 100;
    background-color: #e2b27e;
    border: solid;
    /* border-image: url("/borderbox_simple.png") 25 / 10px 10px 10px 10px stretch; */
    padding: 15px;
}
  
.dialogTitle {
    font-size: 1.1rem;
    font-weight: bold;
    margin-bottom: 15px;
}
  
.dialogFooter {
    cursor: pointer;
    font-size: 0.9rem;
    margin-top: 15px;
    text-align: end;
}

// fade-out animation (ref: https://codepen.io/Brisingr_1/pen/NWwjWLx)
.fade-wrapper {
    transition: opacity ease-out;
    transition-duration: 0.8s;
}